import Vue from "vue";
import firebase from "firebase/app";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    models: {},
    nameNewModel: "",
    progressUpload: {
      file: 0,
      preview: 0
    },
    isUploading: {
      model: false,
      file: false,
      preview: false
    },
    isUploadEnd: {
      model: false,
      file: false,
      preview: false
    },
    nameDeletedModel: "",
    isDeleteState: false
  },
  getters: {
    getModels: state => state.models,
    getProgressUpload: state => state.progressUpload,
    getUploading: state => state.isUploading,
    getUploadEnd: state => state.isUploadEnd,
    getNameNewModel: state => state.nameNewModel,
    getNameDeletedModel: state => state.nameDeletedModel,
    getDeleteState: state => state.isDeleteState
  },
  mutations: {
    fetchModels(state, models) {
      state.models = Object.assign({}, { ...models });
    },
    uploadModel(state, model) {
      Vue.set(state.models, model.key, model);
    },
    updateModel(state, { key, model }) {
      state.models[key] = model;
    },
    deleteModel(state, key) {
      Vue.delete(state.models, key);
    },
    setProgressUpload(state, { type, payload }) {
      state.progressUpload[type] = payload;
    },
    setUploading(state, { type, payload }) {
      state.isUploading[type] = payload;
    },
    setUploadEnd(state, { type, payload }) {
      state.isUploadEnd[type] = payload;
    },
    setNameNewModel(state, name) {
      state.nameNewModel = name;
    },
    setDeleteState(state, deleteState) {
      state.isDeleteState = deleteState;
    },
    setNameDeletedModel(state, name) {
      state.nameDeletedModel = name;
    }
  },
  actions: {
    async fetchModels({ commit }) {
      const models = await firebase
        .database()
        .ref("models")
        .once("value");

      commit("fetchModels", models.val());
    },
    uploadModel({ state, commit }, { file, name, preview }) {
      return new Promise(async resolve => {
        commit("setUploading", { type: "model", payload: true });
        commit("setUploadEnd", { type: "model", payload: false });
        const dataDb = await firebase
          .database()
          .ref("models")
          .push({ name });

        const key = dataDb.key;
        await firebase
          .database()
          .ref("models")
          .child(key)
          .update({ key });
        let model = { name, key };

        const fileExt = file.name.slice(file.name.lastIndexOf("."));
        const fileUploadTask = firebase
          .storage()
          .ref(`models/${key}${fileExt}`)
          .put(file);

        commit("setUploading", { type: "file", payload: true });
        commit("setProgressUpload", { type: "file", payload: 0 });
        commit("setUploadEnd", { type: "file", payload: false });
        fileUploadTask.on(
          "state_changed",
          sp => {
            commit("setProgressUpload", {
              type: "file",
              payload: Math.floor((sp.bytesTransferred / sp.totalBytes) * 100)
            });
          },
          null,
          async () => {
            const url = await fileUploadTask.snapshot.ref.getDownloadURL();

            await firebase
              .database()
              .ref("models")
              .child(key)
              .update({ url });

            model = { ...model, url };
            commit("setUploading", { type: "file", payload: false });
            commit("setUploadEnd", { type: "file", payload: true });
            uploadCallback("preview");
          }
        );

        const previewExt = preview.name.slice(preview.name.lastIndexOf("."));
        const previewUploadTask = firebase
          .storage()
          .ref(`previews/${key}${previewExt}`)
          .put(preview);

        commit("setUploading", { type: "preview", payload: true });
        commit("setProgressUpload", { type: "preview", payload: 0 });
        commit("setUploadEnd", { type: "preview", payload: false });
        previewUploadTask.on(
          "state_changed",
          sp => {
            commit("setProgressUpload", {
              type: "preview",
              payload: Math.floor((sp.bytesTransferred / sp.totalBytes) * 100)
            });
          },
          null,
          async () => {
            const preview = await previewUploadTask.snapshot.ref.getDownloadURL();

            await firebase
              .database()
              .ref("models")
              .child(key)
              .update({ preview });

            model = { ...model, preview };
            commit("setUploading", { type: "preview", payload: false });
            commit("setUploadEnd", { type: "preview", payload: true });

            uploadCallback("file");
          }
        );

        const uploadCallback = type => {
          if (state.isUploadEnd[type]) {
            commit("uploadModel", model);
            commit("setUploading", { type: "model", payload: false });
            commit("setUploadEnd", { type: "model", payload: true });
            resolve(true);
          }
        };
      });
    },
    updatePreview({ state, commit }, { key, preview }) {
      return new Promise(async resolve => {
        const previewExt = preview.name.slice(preview.name.lastIndexOf("."));
        const previewUploadTask = firebase
          .storage()
          .ref(`previews/${key}${previewExt}`)
          .put(preview);

        commit("setUploading", { type: "preview", payload: true });
        commit("setProgressUpload", { type: "preview", payload: 0 });
        commit("setUploadEnd", { type: "preview", payload: false });
        previewUploadTask.on(
          "state_changed",
          sp => {
            commit("setProgressUpload", {
              type: "preview",
              payload: Math.floor((sp.bytesTransferred / sp.totalBytes) * 100)
            });
          },
          null,
          async () => {
            const preview = await previewUploadTask.snapshot.ref.getDownloadURL();

            await firebase
              .database()
              .ref("models")
              .child(key)
              .update({ preview });

            const model = { ...state.models[key], preview };
            commit("setUploading", { type: "preview", payload: false });
            commit("setUploadEnd", { type: "preview", payload: true });
            commit("updateModel", { key, model });
            resolve(true);
          }
        );
      });
    },
    async deleteModel({ commit }, key) {
      /*await firebase
        .database()
        .ref()
        .child(`models/${key}`)
        .remove();
      await firebase
        .storage()
        .ref()
        .child(`models/${key}.usdz`)
        .delete();*/
      await firebase
        .storage()
        .ref()
        .child(`previews/${key}`)
        .delete();
      /*commit("deleteModel", key);
      commit("setDeleteState", true);*/
    }
  }
});
