<template>
  <div>
    <h3 class="headline mb-2">
      AR Gallery
    </h3>
    <v-layout wrap>
      <v-flex 
        v-for="model in models"
        :key="model.key"
        xs12 sm6 md3
        class="pa-2"  
      >
        <v-card>
          <div
            class="card__preview"
            :style="{
              'background-image': `url(${model.preview})`
            }"
          >
            <div
              v-if="!model.preview"
              class="card__preview-stub"
            ></div>
            <v-btn
              v-if="!model.preview"
              class="card__preview-add"
              fab flat
              @click="openUpdatePreview(model.key)"
            >
              <v-icon>add</v-icon>
            </v-btn>
          </div>
          <v-layout justify-space-between align-center>
            <a
              :href="model.url" 
              fab flat
              class="v-btn v-btn--flat v-btn--floating theme--light success--text"
              rel="ar"
            >
              <img style="display: none;" src="" alt="">
              <v-icon color="success">open_in_new</v-icon>
            </a>
            <h4 class="subheading">{{ model.name }}</h4>
            <v-btn
              @click="openDeleteModal(model.key)"
              fab flat 
              color="error"
            >
              <v-icon>close</v-icon>
            </v-btn>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>

    <v-alert
      :value="isDeleteState"
      @input="setDeleteState(false)"
      dismissible
      type="error"
      transition="fade-transition"
      class="alert"
    >
      {{ this.nameModelDeleted }} was successfully deleted!
    </v-alert>

    <v-dialog
      v-model="updatePreviewDialog"
      fullscreen
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="updatePreviewDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Upload Model</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              dark flat
              :loading="uploading.preview"
              :disabled="!previewFile || uploading.preview"
              @click="savePreview"
            >
              Save
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>

        <v-list two-line subheader>
          <v-subheader>Select picture</v-subheader>
          <v-list-tile v-if="!previewFile">
            <v-btn
              color="info"
              class="ma-0"
              @click="$refs.previewFile.click()"
            >
              Select
            </v-btn>
            <input
              type="file"
              accept="image/*"
              ref="previewFile"
              :multiple="false"
              @change="detectPreview"
            />
          </v-list-tile>
          <v-list-tile v-show="previewFile" class="list-custom">
            <v-list-tile-content>
              <img
                class="picture"
                ref="previewImg"
                src=""
                alt="Image preview"
              >
            </v-list-tile-content>

            <v-list-tile-action>
              <v-btn
                v-if="progressUpload.preview === 0"
                fab flat
                color="error"
                @click="modelFile = null"
              >
                <v-icon>close</v-icon>
              </v-btn>
              <v-btn
                v-if="progressUpload.preview === 100"
                fab flat
              >
                <v-icon>done</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>

        <v-progress-linear
          v-if="progressUpload.preview > 0 && progressUpload.preview < 100"
          :value="progressUpload.preview"
        ></v-progress-linear>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">Are you sure to delete {{ nameModelDeleted }}?</v-card-title>

        <v-card-actions>
          <v-btn
            @click="deleteModel(deleteKey); closeDeleteModal();"
            color="success"
          >
            <v-icon>done</v-icon>
          </v-btn>

          <v-spacer></v-spacer>

          <v-btn
            @click="closeDeleteModal()"
            color="error"
          >
            <v-icon>close</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";

export default {
  data() {
    return {
      updatePreviewDialog: false,
      dialog: false,
      selectedModelKey: "",
      previewFile: null
    };
  },
  computed: {
    ...mapGetters({
      models: "getModels",
      nameModelDeleted: "getNameDeletedModel",
      isDeleteState: "getDeleteState",
      progressUpload: "getProgressUpload",
      uploading: "getUploading",
      uploadEnd: "getUploadEnd"
    })
  },
  created() {
    this.fetchModels();
  },
  methods: {
    ...mapMutations(["setDeleteState", "setNameDeletedModel"]),
    ...mapActions(["fetchModels", "deleteModel", "updatePreview"]),
    openDeleteModal(key) {
      this.dialog = true;
      this.deleteKey = key;
      this.setDeleteState(false);
      this.setNameDeletedModel(this.models[key].name);
    },
    closeDeleteModal() {
      this.dialog = false;
    },
    openUpdatePreview(key) {
      this.selectedModelKey = key;
      this.updatePreviewDialog = true;
    },
    detectPreview(e) {
      let fileList = e.target.files || e.dataTransfer.files;
      this.previewFile = fileList[0];

      const preview = this.$refs.previewImg;
      const reader = new FileReader();
      reader.onloadend = () => {
        preview.src = reader.result;
      };
      reader.readAsDataURL(fileList[0]);
    },
    async savePreview() {
      const response = await this.updatePreview({
        key: this.selectedModelKey,
        preview: this.previewFile
      });
      if (response) this.updatePreviewDialog = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.subheading {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__preview {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  &::before {
    content: "";
    display: block;
    padding-top: 75%;
  }

  &-add {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &-stub {
    position: absolute;
    top: 20px;
    bottom: 20px;
    left: 20px;
    right: 20px;
    border: 3px dashed #e5e5e5;
    border-radius: 5px;
  }
}
</style>
