<template>
  <div>
    <h3 class="headline mb-2">
      Upload a 3D-model
    </h3>
    <v-btn
      @click.native="uploadingDialog = true"
      color="info"
    >
      Upload
      <v-icon right aria-hidden="true">backup</v-icon>
    </v-btn>

    <v-dialog
      v-model="uploadingDialog"
      fullscreen
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="uploadingDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Upload Model</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              dark flat
              :loading="uploading.model"
              :disabled="!(valid && modelFile) || uploading.model"
              @click="saveModel"
            >
              Save
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>

        <v-list two-line>
          <v-subheader>Set model name</v-subheader>
          <v-list-tile>
            <v-list-tile-content>
              <v-form v-model="valid">
                <v-text-field
                  v-model="name"
                  :rules="[v => !!v || 'Name is required']"
                  label="Name"
                  required
                ></v-text-field>
              </v-form>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>

        <v-divider></v-divider>

        <v-list two-line subheader>
          <v-subheader>Select model</v-subheader>
          <v-list-tile v-if="!modelFile">
            <v-btn
              color="info"
              class="ma-0"
              @click="$refs.modelFile.click()"
            >
              Select
            </v-btn>
            <input
              type="file"
              accept=".usdz"
              ref="modelFile"
              :multiple="false"
              @change="detectModel"
            />
          </v-list-tile>
          <v-list-tile v-if="modelFile" avatar>
            <v-list-tile-avatar>
              <img src="@/assets/images/usdz.png" alt="usdz">
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title v-html="modelFile ? modelFile.name : ''"></v-list-tile-title>
              <v-list-tile-sub-title v-html="modelFile ? $options.filters.size(modelFile.size, 3) : ''"></v-list-tile-sub-title>
            </v-list-tile-content>

            <v-list-tile-action>
              <v-btn
                v-if="progressUpload.file === 0"
                fab flat
                color="error"
                @click="modelFile = null"
              >
                <v-icon>close</v-icon>
              </v-btn>
              <v-btn
                v-if="progressUpload.file === 100"
                fab flat
              >
                <v-icon>done</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>

        <v-progress-linear
          v-if="progressUpload.file > 0 && progressUpload.file < 100"
          :value="progressUpload.file"
        ></v-progress-linear>
        <v-divider></v-divider>

        <v-list two-line subheader>
          <v-subheader>Select picture</v-subheader>
          <v-list-tile v-if="!pictureFile">
            <v-btn
              color="info"
              class="ma-0"
              @click="$refs.pictureFile.click()"
            >
              Select
            </v-btn>
            <input
              type="file"
              accept="image/*"
              ref="pictureFile"
              :multiple="false"
              @change="detectPicture"
            />
          </v-list-tile>
          <v-list-tile v-show="pictureFile" class="list-custom">
            <v-list-tile-content>
              <img
                class="picture"
                ref="previewImg"
                src=""
                alt="Image preview"
              >
            </v-list-tile-content>

            <v-list-tile-action>
              <v-btn
                v-if="progressUpload.preview === 0"
                fab flat
                color="error"
                @click="modelFile = null"
              >
                <v-icon>close</v-icon>
              </v-btn>
              <v-btn
                v-if="progressUpload.preview === 100"
                fab flat
              >
                <v-icon>done</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>

        <v-progress-linear
            v-if="progressUpload.preview > 0 && progressUpload.preview < 100"
            :value="progressUpload.preview"
        ></v-progress-linear>
        <v-divider></v-divider>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";

export default {
  data: () => ({
    valid: false,
    name: "",
    uploadingDialog: false,
    modelFile: null,
    pictureFile: null
  }),
  computed: {
    ...mapGetters({
      progressUpload: "getProgressUpload",
      fileName: "getNameNewModel",
      uploading: "getUploading",
      uploadEnd: "getUploadEnd"
    })
  },
  methods: {
    ...mapMutations(["setUploadEnd"]),
    ...mapActions(["uploadModel", "updatePreview"]),
    detectModel(e) {
      let fileList = e.target.files || e.dataTransfer.files;
      this.modelFile = fileList[0];
    },
    detectPicture(e) {
      let fileList = e.target.files || e.dataTransfer.files;
      this.pictureFile = fileList[0];

      const preview = this.$refs.previewImg;
      const reader = new FileReader();
      reader.onloadend = () => {
        preview.src = reader.result;
      };
      reader.readAsDataURL(fileList[0]);
    },
    async saveModel() {
      const response = await this.uploadModel({
        name: this.name,
        file: this.modelFile,
        preview: this.pictureFile
      });
      if (response) this.uploadingDialog = false;
    },
  }
};
</script>

<style lang="scss">
.progress-bar {
  margin: 10px 0;
}

input[type="file"] {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.alert {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.list-custom {
  .v-list__tile {
    height: auto !important;
  }
}

.picture {
  width: 50%;
}
</style>
